
canvas {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 0;
	width: 100%;
	height: 100%;
	pointer-events: none;
}
#wrapper {
	position: absolute;
	left: 0;
	width: 320px;
	text-align: center;
	top: 50%;
	left: 50%;
	margin-left: -160px;
	margin-top: -160px;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	    user-select: none;
}





